









import { Component, Prop, Vue, Emit } from 'vue-property-decorator';
import { Realtime, TextMessage, Event } from 'leancloud-realtime';
const AV = require('leancloud-storage');
const {
    TypedMessagesPlugin,
    ImageMessage,
} = require('leancloud-realtime-plugin-typed-messages');
import { findUser } from '@/service/index';

@Component
export default class App extends Vue {
    @Emit()
    private init() {
        AV.init({
            appId: '2Fhhh5lcxtGwBOtsXvnsw06G-gzGzoHsz',
            appKey: 'tFBBgTyzjbbywyj6PwK6KGDC',
        });
        const realtime = new Realtime({
            appId: '2Fhhh5lcxtGwBOtsXvnsw06G-gzGzoHsz',
            appKey: 'tFBBgTyzjbbywyj6PwK6KGDC',
            plugins: [TypedMessagesPlugin], // 注册富媒体消息插件
            pushOfflineMessages: true,
        });
        this.$store.commit('init', {
            AV,
            realtime,
        });
        setTimeout(() => {
            this.isLogin();
        }, 1500);
    }
    private isLogin() {
        const currentUser = AV.User.current();
        if (currentUser) {
            this.$store.commit('initAvUser', currentUser);
            console.log(currentUser)
            this.$store.state.realtime
                .createIMClient(currentUser)
                .then((currentUser: any) => {
                    findUser(currentUser.id).then((user: any) => {
                        currentUser.username = user.attributes.username;
                        this.$store.commit('initUser', currentUser);
                        this.$router.push({
                            path: 'diss',
                            query: { topBarName: 'Diss' },
                        });

                        // 跳转到首页
                    });
                });
        } else {
            this.$router.push('user');
            // currentUser 为空时，可打开用户注册界面…
        }
    }
    private created() {
        this.init();
    }
}
